// mockjs
const Mock = require('mockjs')
//  mock.Random 
const Random = Mock.Random
// mock
const userInformData = function () {
  let userInform = []
  for (let i = 0; i < 50; i++) {
    var tel = Mock.mock({
      mobile: /^1[5-7]{1}[0-9]{9}$/
    })
    var lane = Mock.mock({
      'number|1-40': 1
    })
    var floor = Mock.mock({
      'number|1-10': 1
    })
    var level = Mock.mock({
      'number|1-7': 1
    })
    var room = Mock.mock({
      'number|1-5': 1
    })
    let newArticleObject = {
      name: Random.cname(), // Random.cname() 
      sex: Random.cword(''),
      tel: tel.mobile,
      date: Random.date() + ' ' + Random.time(), // Random.date(),yyyy-MM-ddRandom.time() 
      address: ' ' + lane.number + ' ' + floor.number + ' ' + level.number + '0' + room.number
    }
    userInform.push(newArticleObject)
  }

  return {
    userInform: userInform
  }
}

// Mock.mock( url, post/get , )
Mock.mock('/user/inform', 'post', userInformData)



